@import "../helpers/mixins";
@import "../base/variables";

.custom-widget {
	margin-bottom: 100px;

	&__title {
		font-family: $font-ubuntu;
		font-weight: 500;
		font-size: 20px;
		line-height: 20px;
		text-align: center;
		padding: 20px 15px;
		background: rgba(#6D156C, .6);
		border-radius: 15px 15px 0 0; }

	&__casino {
		max-width: 270px;
		margin: 0 auto;

		&-list {
			counter-reset: cas; }

		&-item {
			padding: 30px 23px;
			background-color: $color-white;
			border: 2px solid rgba(46, 71, 215, 0.5);
			border-radius: 30px;

			&:not(:last-child) {
				margin-bottom: 20px; }

			.percent-bar {

				&::before {
					color: $color-accent; } } }

		&-img {
			display: block;
			width: 150px;
			margin: 0 auto 30px;
			position: relative;

			&::before {
				position: absolute;
				counter-increment: cas;
				content: counter(cas);
				top: 0;
				left: -35px;
				width: 50px;
				text-align: center;
				font-family: $font-montserrat;
				font-weight: 700;
				font-size: 60px;
				line-height: 54px;
				background: linear-gradient(135deg, #00d0ff 0%,#fff 75%,#fff 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00d0ff', endColorstr='#ffffff',GradientType=1 );
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent; } }

		&-nav {
			display: flex;
			justify-content: space-between;

			.btn-border,
			.btn-site {
				width: 105px;
				line-height: 16px; } } }


	&__slot {
		max-width: 270px;
		margin: 0 auto;

		&-list {
			counter-reset: list; }

		&-item {
			margin-bottom: 10px;

			&:first-child {

				.custom-widget__slot-inner {
					border-radius: 0 0 40px 40px; } } }

		&-inner {
			padding-bottom: 15px;
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
			position: relative;
			background: #fff;
			border-radius: 40px;
			overflow: hidden; }

		&-thumb {
			width: 280px;
			position: relative;
			transform: rotate(-10deg);
			overflow: hidden;
			top: -23px;
			left: -3px;
			border-bottom-left-radius: 60px;
			margin-bottom: 15px;

			img {
				transform: rotate(10deg);
				position: relative;
				top: 14px; } }

		&-title {
			padding: 0 20px;
			margin: 0;
			font-size: 18px;
			line-height: 21px;
			font-weight: 500;
			color: #272D2F;
			text-decoration: none;
			text-align: center;
			position: relative;
			@include r(1259) {
				font-size: 16p; }

			&::before {
				position: absolute;
				counter-increment: list;
				content: counter(list);
				width: 56px;
				height: 66px;
				top: -90px;
				right: 20px;
				color: #272D2F;
				font-size: 12px;
				line-height: 53px;
				text-align: center;
				@include svg(slot); } } }


	&__slot-related {

		&-item {
			display: flex;
			align-items: center;
			padding: 10px 15px;
			width: 100%;
			color: $color-text;
			text-decoration: none;
			background-color: $color-white;
			border-radius: $border-radius;
			cursor: pointer;

			&:not(:last-child) {
				margin-bottom: 15px; }

			&:hover {
				color: $color-blue; } }

		&-img {
			flex-shrink: 0;
			position: relative;
			margin-right: 15px;

			img {
				border-radius: 50%;
				border: 2px solid $color-white;
				position: relative; }

			&::before {
				position: absolute;
				content: '';
				width: 59px;
				height: 51px;
				top: 0;
				left: -3px;
				@include svg(slot-related); } }

		&-text {
			display: flex;
			flex-direction: column;
			justify-content: center;
			flex-grow: 1; }

		&-title {
			color: inherit;
			font-size: 17px;
			line-height: 22px;
			pointer-events: none; }

		&-descr {
			color: $color-dark;
			font-size: 14px;
			line-height: 20px; } }


	&__slot-day {
		max-width: 270px;
		margin: 0 auto;

		&-item {
			display: block;
			text-decoration: none; }

		&-img {
			border-radius: 30px 30px 0 0; }

		&-text {
			width: 100%;
			display: flex;
			padding: 30px 15px 15px;
			text-align: center;
			flex-direction: column;
			background-color: $color-white;
			border-radius: 30px;
			position: relative;
			top: -30px;

			&::before {
				position: absolute;
				content: 'top';
				top: -15px;
				left: 50%;
				transform: translateX(-50%);
				padding: 5px 15px;
				color: $color-white;
				font-size: 14px;
				line-height: 20px;
				text-align: center;
				text-transform: uppercase;
				background-color: $color-blue;
				border-radius: 15px; } }

		&-title {
			color: $color-text;
			font-size: 20px;
			line-height: 22px;
			font-weight: 500;
			margin-bottom: 15px; }

		&-wins,
		&-total {
			color: $color-dark;
			font-size: 17px;
			line-height: 24px;

			span {
				font-family: $font-montserrat;
				font-weight: 700;
				font-size: 30px;
				line-height: 36px;
				background-image: linear-gradient(135deg, #2e47d7 0%,#fff 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2e47d7', endColorstr='#ffffff',GradientType=1 );
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent; } }

		&-wins {
			margin-bottom: 7px; } }


	&__dev {
		max-width: 270px;
		margin: 0 auto 100px;

		&-list {
			padding: 15px;
			background-color: rgba($color-white, .25);
			border-radius: 0 0 15px 15px; }

		&-item {
			display: block;
			text-decoration: none;

			&:not(:last-child) {
				margin-bottom: 10px; } }

		&-img {
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 20px;
			height: 90px;
			background-color: $color-text;
			border-radius: $border-radius;
			position: relative;
			z-index: 1;

			img {
				max-height: 100%; } }

		&-title {
			width: 110px;
			display: flex;
			align-items: center;
			padding: 5px 10px;
			color: $color-text;
			font-size: 12px;
			line-height: 14px;
			text-align: center;
			background-color: rgba($color-white, .35);
			border-radius: 0 0 10px 10px;
			position: relative;
			top: 0;
			left: 50%;
			transform: translateX(-50%);

			svg,
			span {
				margin-right: 5px; } } } }

.widget-news {

	.widget-content {
		padding: 15px 20px;
		background-color: $color-white;
		border: 2px solid $color-border;
		border-radius: $border-radius; }

	.widget-news-item {

		&:not(:last-child) {
			margin-bottom: 10px; } }

	.widget-news-item-last {
		text-align: center; }

	.widget-news-date {
		color: $color-grey;
		font-size: 14px;
		line-height: 25px;
		margin-bottom: 5px; }

	.widget-news-title {

		a {
			color: $color-text;
			text-decoration: none;

			&:hover {
				color: $color-accent; } } } }
