.footer {

	&__wrapper {
		padding: 40px 0;
		background-color: #6D156C; }

	&__top {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		margin-bottom: 45px;
		@include r(991) {
			flex-direction: column;
			margin-bottom: 30px; } }

	&__logo {
		width: 230px;
		margin-right: 70px;
		flex-shrink: 0;
		@include r(991) {
			margin-bottom: 30px; } }

	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include r(767) {
			flex-direction: column; } }

	&__copyright,
	&__limit {
		font-size: 14px;
		line-height: 25px;
		color: #D995DC;

		span {
			color: $color-grey; } }

	&__copyright {
		margin-right: 65px;
		@include r(767) {
			margin-right: 0;
			order: 2;
			text-align: center; } }

	&__limit {
		//padding-left: 46px
		position: relative;
		@include r(767) {
			margin-bottom: 20px;
			text-align: center; }

		//&::before
		//	position: absolute
